.cost-optimizations-card .empty-state .empty-icon {
  color: #d1d5db;
  margin-bottom: 1rem;
}
.cost-optimizations-card .empty-state .empty-title {
  font-size: 1.125rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 0.5rem 0;
}
.cost-optimizations-card .empty-state .empty-description {
  font-size: 0.875rem;
  color: #6b7280;
  margin: 0;
  line-height: 1.5;
}

@media (max-width: 768px) {
  .cost-optimizations-card {
    padding: 1rem;
  }
  .cost-optimizations-card .card-header {
    flex-direction: column;
    align-items: stretch;
    gap: 0.5rem;
    text-align: center;
  }
  .cost-optimizations-card .optimizations-list .optimization-item .optimization-header {
